import * as urls from './utils.js'
import axios from '../config/http'

// 接口封装
function request (url = '', data = {}, type = 'GET', config) {
  type = type.toLowerCase()
  axios.defaults.baseURL = 'http://192.168.1.106:8004'
  return axios[type](url, data, config).then(function (response) {
    return response.data
  }).catch(function (e) {
    return e
  })
}

