import Vue from 'vue'
import Router from 'vue-router'
import store from '@/vuex'

Vue.use(Router)

let routers = new Router({
  mode: 'history',
  base: '/selectCourse-manager/',
  routes: [
    {
      path: '/',
      name: 'login',
      component: resolve => require(['@/pages/Login'], resolve)
    },
    {
      path: '/main',
      name: 'main',
      component: resolve => require(['@/components/Main'], resolve),
      children: [
        {
          path: '/gymlist',
          name: 'gymlist',
          component: resolve => require(['@/pages/gymList'], resolve)
        },
        {
          path: '/addgymbranch',
          name: 'addgymbranch',
          component: resolve => require(['@/pages/addgymbranch'], resolve)
        },
        {
          path: '/coachlist',
          name: 'coachlist',
          component: resolve => require(['@/pages/coachlist'], resolve)
        },
        {
          path: '/waitvericoach',
          name: 'waitvericoach',
          component: resolve => require(['@/pages/waitvericoach'], resolve)
        },
        {
          path: '/updatecoach',
          name: 'updatecoach',
          component: resolve => require(['@/pages/updatecoach'], resolve)
        },
        {
          path: '/privatecourse',
          name: 'privatecourse',
          component: resolve => require(['@/pages/privatecourse'], resolve)
        },
        {
          path: '/privatekind',
          name: 'privatekind',
          component: resolve => require(['@/pages/privatekind'], resolve)
        },
        {
          path: '/addprivatekind',
          name: 'addprivatekind',
          component: resolve => require(['@/pages/addprivatekind'], resolve)
        },
        {
          path: '/studentlist',
          name: 'studentlist',
          component: resolve => require(['@/pages/studentlist'], resolve)
        },
        {
          path: '/updatestudent',
          name: 'updatestudent',
          component: resolve => require(['@/pages/updatestudent'], resolve)
        },
        {
          path: '/testcalendar',
          name: 'testcalendar',
          component: resolve => require(['@/pages/testCalendar'], resolve)
        },
        {
          path: '/worklist',
          name: 'worklist',
          component: resolve => require(['@/pages/worklist'], resolve)
        },
        {
          path: '/league',
          name: 'league',
          component: resolve => require(['@/pages/league'], resolve)
        },
        {
          path: '/leaguekind',
          name: 'leaguekind',
          component: resolve => require(['@/pages/leaguekind'], resolve)
        },
        {
          path: '/addleakind',
          name: 'addleakind',
          component: resolve => require(['@/pages/addleakind'], resolve)
        },
        {
          path: '/memberCard',
          name: 'memberCard',
          component: resolve => require(['@/pages/memberCard'], resolve)
        },

        {
          path: '/addmembercard',
          name: 'addmembercard',
          component: resolve => require(['@/pages/addmembercard'], resolve)
        },
        {
          path: '/memberDetail',
          name: 'memberDetail',
          component: resolve => require(['@/pages/memberDetail'], resolve)
        },
        {  
          path: '/vipcard',
          name: 'vipcard',
          component: resolve => require(['@/pages/vipcard'], resolve)
        },
        {
          path: '/addvipkind',
          name: 'addvipkind',
          component: resolve => require(['@/pages/addvipkind'], resolve)
        },
        {
          path: '/salelist',
          name: 'salelist',
          component: resolve => require(['@/pages/salelist'], resolve),
          // children: [
          //   {
          //     path: "detail/:id",
          //     name: "addSale",
          //     component: resolve =>
          //       require(['../pages/salelist/addSale1'], resolve)
          //   }
          // ]
        },
        {
          path: '/leagueAppoint',
          name: 'leagueAppoint',
          component: resolve => require(['@/pages/leagueAppoint'], resolve)
        },
        {  
          path: '/privateLimit',
          name: 'privateLimit',
          component: resolve => require(['@/pages/privateLimit'], resolve)
        },
        {  
          path: '/ImportMember',
          name: 'ImportMember',
          component: resolve => require(['@/pages/ImportMember'], resolve)
        },
        {  
          path: '/ImportCard',
          name: 'ImportCard',
          component: resolve => require(['@/pages/ImportCard'], resolve)
        },
        {  
          path: '/worklistSign',
          name: 'worklistSign',
          component: resolve => require(['@/pages/worklistSign'], resolve)
        },
        {
          path: '/personSet',
          name: 'personSet',
          component: resolve => require(['@/pages/personSet'], resolve)
        },
        {
          path: '/promotion',
          name: 'promotion',
          component: resolve => require(['@/pages/promotion'], resolve)
        },
        {
          path: '/addpromotion',
          name: 'addpromotion',
          component: resolve => require(['@/pages/addpromotion'], resolve)
        },
        {
          path: '/cardDetail',
          name: 'cardDetail',
          component: resolve => require(['@/pages/cardDetail'], resolve)
        },
        {
          path: '/statistics',
          name: 'statistics',
          component: resolve => require(['@/pages/statistics'], resolve)
        },
        {path: '/sneakGuest',
          name: 'sneakGuest',
          component: resolve => require(['@/pages/sneakGuest'], resolve)
        },
        {
          path: '/sneakGuestEdit',
          name: 'sneakGuestEdit',
          component: resolve => require(['@/pages/sneakGuestEdit'], resolve)
        },
        {
          path: '/addSale',
          name: 'addSale',
          component: resolve => require(['@/pages/addSale'], resolve)
        },
        {
          path: '/entranceSign',
          name: 'entranceSign',
          component: resolve => require(['@/pages/entranceSign'], resolve)
        },
        {
          path: '/student',
          name: 'student',
          component: resolve => require(['@/pages/student'], resolve)
        },
        {
          path: '/teacher',
          name: 'teacher',
          component: resolve => require(['@/pages/teacher'], resolve)
        },
        {
          path: '/courseList',
          name: 'courseList',
          component: resolve => require(['@/pages/courseList'], resolve)
        },
        {
          path: '/selectCourseList',
          name: 'selectCourseList',
          component: resolve => require(['@/pages/selectCourseList'], resolve)
        },
        {
          path: '/studentSelectCourse',
          name: 'studentSelectCourse',
          component: resolve => require(['@/pages/studentSelectCourse'], resolve)
        },
        {
          path: '/studentDetail',
          name: 'studentDetail',
          component: resolve => require(['@/pages/studentDetail'], resolve)
        },
        {
          path: '/teacherDetail',
          name: 'teacherDetail',
          component: resolve => require(['@/pages/teacherDetail'], resolve)
        },
        {
          path: '/courseDetail',
          name: 'courseDetail',
          component: resolve => require(['@/pages/courseDetail'], resolve)
        },
        {
          path: '/selectCourseDetail',
          name: 'selectCourseDetail',
          component: resolve => require(['@/pages/selectCourseDetail'], resolve)
        }
      ]
    }
  ]
})

routers.beforeEach((to, from, next) => {
  if (!store.getters.getToken && to.path !== '/') {
    next({name: 'login'})
  } else if (store.getters.getToken && to.path !== '/') {
    next()
  } else {
    next()
  }
})

export default routers



