import axios from 'axios'
import qs from 'qs'
import store from '@/vuex'
import router from '@/router'
import Vue from 'vue'

axios.defaults.timeout = 20000
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
// axios.defaults.baseURL = 'https://mainapi.icarbonx.com'
axios.defaults.withCredentials=true
axios.defaults.crossDomain = true
axios.defaults.headers['Cache-Control'] = 'no-cache'
axios.defaults.headers['If-Modified-Since'] = '0'

// POST传参序列化
axios.interceptors.request.use((config) => {
  var str = config.url
  // console.log(axios.defaults.timeout,'-----------')
  let arr = config.url.split('?')
  if (arr[1]) {
    let str = arr[1]
    let temp = str.split('&')
    for (var i=0; i<temp.length; i++) {
      let arr2 = temp[i].split('=')
      if (!arr2[1] || arr2[1] == 'null') {
        temp.splice(i, 1)
        i = i-1
      }
    }
    let string = temp.join('&')
    if (string) {
      config.url = arr[0]+'?'+string
    } else {
      config.url = arr[0]
    }
  }
  for (let key in config.data) {
    if (config.data[key]) {
      config.data[key] = config.data[key]
    } else {
      delete config.data[key]
    }
  }
  // type是为了有的接口希望RequestBody是json
  if (config.method === 'post' && config.type !== 'json') {
    config.data = qs.stringify(config.data)
  }
  if (config.type !== 'send') {
    if (config.type === 'login') {
      config.headers['token'] = ''
    } else {
      config.headers['token'] = localStorage.getItem('access_token')
    }
  }
  return config
}, (error) => {
  return Promise.reject(error)
})
// console.log(axios.defaults.timeout)
// http response 拦截器
axios.interceptors.response.use((res) => {
  if (res.request.responseURL && (res.request.responseURL.indexOf('/gymmanage/import/excel') > -1 || res.request.responseURL.indexOf('/gymmanage/import/excel/vipcard/') > -1)) {
    // axios.defaults.timeout = 600000
    if (!res.headers['content-disposition']) {
      var enc = new TextDecoder('utf-8') 
      res.data = JSON.parse(enc.decode(new Uint8Array(res.data))) //转化成json对象
    } else {
      var disposition = res.headers['content-disposition']
      var fileName = decodeURI(disposition.substring(disposition.indexOf('filename=') + 9, disposition.length))
      let blob = new Blob([res.data])
      let link = document.createElement('a')
      link.href = window.URL.createObjectURL(blob)
      link.download = fileName
      link.click()
      link.remove()
    }
  }
  // console.log(axios.defaults.timeout,'/////////////////////////')
  if (res && res.data.code == 401) {
    Vue.prototype.$message({
      type: "warning",
      message: '未登录'
    })
    router.push({name: 'login'})
  } 
  return res
}, (error) => {
  if (error && error.response){
    if (error.config.type === 'login' || error.config.type === 'error') {
      return Promise.reject(error.response.data)
    }
  }
  // return Promise.reject(error)
  Vue.prototype.$message({
    type: "warning",
    message: "程序小哥开小差了，请稍后重试"
  })
})
export default axios



