
export function phoneFormat (str) {
  if (!str) return
  return str.substring(0, 3) + '-' + str.substring(3, 7) + '-' + str.substring(7, 11)
}
export function coachList (list) {
  let coachList = ''
  if (!list) return
  if (list.length > 0) {
    for (var i = 0; i < list.length; i++) {
      coachList = coachList + list[i] + ' '
    }
  }
  return coachList
}
export function timeformer (val) {
  if (!val) return
  var d = new Date()
  d.setTime(val)
  // var hour = d.getHours()
  // var minute = d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes()
  // var second = d.getSeconds() < 10 ? '0' + d.getSeconds() : d.getSeconds()
  let y = d.getFullYear()
  let m = (d.getMonth() + 1) < 10 ? '0' + (d.getMonth() + 1) : (d.getMonth() + 1) // 获取当前月份的日期，不足10补0
  let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate() // 获取当前几号，不足10补0
  let commonTime = y + '-' + m + '-' + day
  return commonTime
}
// 证件类型转换
export function IdFormat (val) {
  if (!val) return
  switch (val) {
    case 'MainlandIDCard':
      return '身份证'
    case 'HongkongIDCard':
      return '香港身份证'
    case 'Passport':
      return '护照'
    case 'TaiBaoCard':
      return '台胞证'
    default:
      return '身份证'
  }
}
// 通过时间戳或者时间
export function getMinuter (val) {
  if (!val) return
  var d = new Date()
  d.setTime(val)
  var hour = d.getHours()
  var minute = d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes()
  // var second = d.getSeconds() < 10 ? '0' + d.getSeconds() : d.getSeconds()
  // let y = d.getFullYear()
  // let m = (d.getMonth() + 1) < 10 ? '0' + (d.getMonth() + 1) : (d.getMonth() + 1) // 获取当前月份的日期，不足10补0
  // let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate() // 获取当前几号，不足10补0
  let commonTime = hour + ':' + minute
  return commonTime
}
// 会员卡类型转换
export function cardType (val) {
  if (!val) return
  switch (val) {
    case 'DATELIMIT':
      return '会籍卡'
    case 'TIMES':
      return '次卡'
    case 'PREPAID':
      return '储值卡'
      case 'PACKAGE':
      return '期限卡'
  }
}
// 会员卡状态转换
export function cardStatus (val) {
  if (!val) return
  switch (val) {
    case 'STOP':
      return '停卡'
    case 'CANCEL':
      return '退卡'
    case 'NORMAL':
      return '正常'
  }
}
// 检查余额是否为空
export function isBalance (val) {
  if (!val) {
    return '--'
  } else {
    return val
  }
}
// 性别转换
export function personSex (val) {
  switch (val) {
    case 0:
      return '男'
    case 1:
      return '女'
  }
}
export function fundFormat (val, unit) {
  let reg = /^((-?)(\d*\.?\d*)|(\d*\.?\d*%))$/ // 整数、小数、百分数
  if (!reg.test(val)) return val
  let str = String(val).indexOf('%') !== -1 ? String(val.substring(0, val.length - 1)) : String(val)
  if (str.indexOf('.') === -1) {
    str = Number(str).toFixed(unit)
  } else if (str.length - 1 - Number(str.indexOf('.')) >= unit) {
    str = str.substring(0, str.indexOf('.') + unit + 1)
  } else {
    let len = unit - (str.length - 1 - Number(str.indexOf('.')))
    for (var i = 0; i < len; i++) {
      str = str + '0'
    }
  }
  return String(val).indexOf('%') !== -1 ? str + '%' : str
}


